'use client'

import { useEffect, useRef } from 'react'
import * as THREE from 'three'
import { useInView } from 'framer-motion'
import './Hero.css'

interface HeroProps {
  onVisible: () => void
}

export default function Hero({ onVisible }: HeroProps) {
  const ref = useRef(null)
  const isInView = useInView(ref, { amount: 0.5 })
  const mousePosition = useRef({ x: 0, y: 0 })

  useEffect(() => {
    if (isInView) {
      onVisible()
    }
  }, [isInView, onVisible])

  // Handle mouse movement
  useEffect(() => {
    const handleMouseMove = (event: MouseEvent) => {
      mousePosition.current = {
        x: (event.clientX / window.innerWidth) * 2 - 1,
        y: -(event.clientY / window.innerHeight) * 2 + 1
      }
    }
    
    window.addEventListener('mousemove', handleMouseMove)
    return () => window.removeEventListener('mousemove', handleMouseMove)
  }, [])

  // âœ… Three.js Scene Setup with Animations
  useEffect(() => {
    const mount = document.querySelector('.hero-canvas-container')
    if (!mount) return

    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    )
    const renderer = new THREE.WebGLRenderer({ 
      alpha: true,
      antialias: true 
    })
    renderer.setSize(window.innerWidth, window.innerHeight)
    renderer.domElement.style.position = 'absolute'
    renderer.domElement.style.top = '0'
    renderer.domElement.style.left = '0'
    renderer.domElement.style.zIndex = '1'
    mount.appendChild(renderer.domElement)

    // âœ… Load Background Image
    const loader = new THREE.TextureLoader()
    let backgroundMesh: THREE.Mesh | null = null
    let particles: THREE.Points | null = null
    let time = 0

    loader.load(
      '/hero-bg.png',
      (texture) => {
        console.log('âœ… Background image loaded successfully!')
        
        // Calculate aspect ratios
        const imageAspect = texture.image.width / texture.image.height
        const screenAspect = window.innerWidth / window.innerHeight
        
        let planeWidth, planeHeight
        const baseSize = 50
        
        if (screenAspect > imageAspect) {
          planeHeight = baseSize
          planeWidth = planeHeight * screenAspect
        } else {
          planeWidth = baseSize
          planeHeight = planeWidth / screenAspect
        }

        const geometry = new THREE.PlaneGeometry(planeWidth, planeHeight)
        const material = new THREE.MeshBasicMaterial({ 
          map: texture,
          transparent: true
        })
        backgroundMesh = new THREE.Mesh(geometry, material)
        backgroundMesh.position.z = -20
        scene.add(backgroundMesh)

        // âœ… Add Floating Particles
        createParticles()
      },
      undefined,
      (error) => console.error('âŒ Failed to load image:', error)
    )

    // âœ… Create Animated Particles
    const createParticles = () => {
      const particleCount = 100
      const positions = new Float32Array(particleCount * 3)
      const colors = new Float32Array(particleCount * 3)

      for (let i = 0; i < particleCount; i++) {
        // Random positions in a sphere
        const i3 = i * 3
        const radius = 15 + Math.random() * 10
        const theta = Math.random() * Math.PI * 2
        const phi = Math.random() * Math.PI
        
        positions[i3] = radius * Math.sin(phi) * Math.cos(theta)
        positions[i3 + 1] = radius * Math.sin(phi) * Math.sin(theta)
        positions[i3 + 2] = radius * Math.cos(phi)
        
        // Gold-ish colors
        colors[i3] = 0.85 + Math.random() * 0.15 // R
        colors[i3 + 1] = 0.65 + Math.random() * 0.2 // G
        colors[i3 + 2] = 0.1 + Math.random() * 0.1 // B
      }

      const particleGeometry = new THREE.BufferGeometry()
      particleGeometry.setAttribute('position', new THREE.BufferAttribute(positions, 3))
      particleGeometry.setAttribute('color', new THREE.BufferAttribute(colors, 3))

      const particleMaterial = new THREE.PointsMaterial({
        size: 0.5,
        vertexColors: true,
        transparent: true,
        opacity: 0.8,
        blending: THREE.AdditiveBlending
      })

      particles = new THREE.Points(particleGeometry, particleMaterial)
      scene.add(particles)
    }

    const ambientLight = new THREE.AmbientLight(0xffffff, 0.8)
    scene.add(ambientLight)

    // âœ… Add subtle directional light for depth
    const directionalLight = new THREE.DirectionalLight(0xd4af37, 0.3)
    directionalLight.position.set(5, 5, 5)
    scene.add(directionalLight)

    camera.position.z = 5

    // âœ… Animation Loop
    const animate = () => {
      requestAnimationFrame(animate)
      time += 0.01

      // Animate background mesh (subtle floating)
      if (backgroundMesh) {
        backgroundMesh.rotation.z = Math.sin(time * 0.2) * 0.02
        backgroundMesh.position.y = Math.sin(time * 0.3) * 0.5
        
        // Mouse parallax effect
        backgroundMesh.position.x = mousePosition.current.x * 0.5
        backgroundMesh.position.y += mousePosition.current.y * 0.3
      }

      // Animate particles
      if (particles) {
        particles.rotation.y = time * 0.1
        particles.rotation.x = Math.sin(time * 0.2) * 0.1
        
        const positions = particles.geometry.attributes.position.array as Float32Array
        for (let i = 0; i < positions.length; i += 3) {
          positions[i + 1] += Math.sin(time + i) * 0.02 // Floating motion
        }
        particles.geometry.attributes.position.needsUpdate = true
      }

      renderer.render(scene, camera)
    }
    animate()

    // âœ… Resize Handling
    const handleResize = () => {
      const width = window.innerWidth
      const height = window.innerHeight
      
      camera.aspect = width / height
      camera.updateProjectionMatrix()
      renderer.setSize(width, height)
      
      if (backgroundMesh) {
        const imageAspect = backgroundMesh.material.map?.image.width / backgroundMesh.material.map?.image.height || 1
        const screenAspect = width / height
        
        let planeWidth, planeHeight
        const baseSize = 50
        
        if (screenAspect > imageAspect) {
          planeHeight = baseSize
          planeWidth = planeHeight * screenAspect
        } else {
          planeWidth = baseSize
          planeHeight = planeWidth / screenAspect
        }
        
        backgroundMesh.geometry.dispose()
        backgroundMesh.geometry = new THREE.PlaneGeometry(planeWidth, planeHeight)
      }
    }
    
    window.addEventListener('resize', handleResize)

    return () => {
      window.removeEventListener('resize', handleResize)
      if (mount.contains(renderer.domElement)) {
        mount.removeChild(renderer.domElement)
      }
      renderer.dispose()
    }
  }, [])

  return (
    <section id="hero" ref={ref} className="hero-section">
      <div className="hero-canvas-container"></div>
      <div className="hero-container">
        <h1 className="hero-title">
          Crafting <span className="hero-highlight">Divine</span> Digital Experiences
        </h1>
        <p className="hero-subtitle">
          We are a full-service digital agency creating stunning websites, powerful applications,
          and unforgettable brand experiences that drive results and elevate your digital presence.
        </p>
        <div className="hero-stats">
          <div className="stat-item">
            <div className="stat-number">150+</div>
            <div className="stat-label">Projects Completed</div>
          </div>
          <div className="stat-item">
            <div className="stat-number">98%</div>
            <div className="stat-label">Client Satisfaction</div>
          </div>
          <div className="stat-item">
            <div className="stat-number">5+</div>
            <div className="stat-label">Years Experience</div>
          </div>
        </div>
        <div className="hero-actions">
          <button className="hero-btn primary">Start Your Project</button>
          <button className="hero-btn secondary">View Our Work</button>
        </div>
        <div className="hero-scroll-indicator">
          <div className="scroll-arrow"></div>
        </div>
      </div>
    </section>
  )
}